import React, { Component } from "react";
import { NavLink } from "react-router-dom";

import file1 from "./shared/images/project1.jpg";
import My from "../images/myphoto.jpg";
class home extends Component {
  state = {};
  render() {
    return (
      <React.Fragment>
        <div className="container">
          <div className="row">
            <div className="col text-center">
              <h1>
                <span
                  className="badge badge-primary"
                  style={{ fontSize: 50, color: "black" }}
                >
                  Welcome
                </span>
              </h1>
              <hr
                style={{ width: 150, borderTop: "2px solid black", height: 30 }}
              />
            </div>
          </div>
        </div>
        <div className="jumbotron">
          <div className="container">
            <div className="row">
              <div className="col-8">
                <h1 className="display-4 ">Hey! I am Harsh Kaushik.</h1>
                <p className="lead">I am a Web Designer.</p>
                <p>
                  I Like to design Website, Please Take a look what kind of
                  person I am.
                </p>
                <p className="lead">
                  <NavLink
                    to="/about"
                    className="btn btn-outline-dark btn-lg"
                    role="button"
                  >
                    About Me
                  </NavLink>
                </p>
              </div>
              <div className="col-4 justify-content-end">
                <div
                  className="card bg-dark d-flex align-self-end"
                  style={{ width: 220, marginLeft: 50 }}
                >
                  <img
                    className="card-img-top card-img-overlay img-fluid"
                    src={My}
                    alt="Card image cap"
                  />
                  <div className="card-body">
                    <h5 className="card-title"></h5>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt-5">
              <div className="col text-center mt-5">
              <hr
                style={{ width: 150, borderTop: "2px solid black", height: 10 }}
              />
                <h1>
                  <span
                    className="badge badge-primary"
                    style={{ fontSize: 50, color: "black", marginTop: 10 }}
                  >
                    Project
                  </span>
                </h1>
                <div
                  className="card bg-darken-1 mb-3 ml-5 mt-5"
                  style={{ width: 1000 }}
                >
                  <img
                    className="card-img-top"
                    src={file1}
                    style={{ width: "1000", height: 600 }}
                    alt="Card image cap"
                  />
                  <div className="card-body ">
                    <h5 className="card-title">
                      It is a Single Page APPLICATION
                    </h5>
                    <p className="card-text">
                      Project is totally buid in three languages i.e. HTML
                      5,CSS,JAVASCRIPT.
                      <br />
                      Its is totally simple and efficient project which working
                      on searches of different items like
                      Cakes,Cupckaes,Doughnuts etc.
                      <br />
                      <span className="badge badge-danger">
                        I got 92 marks out of 100 in this project
                      </span>
                    </p>
                    <p className="card-text">
                      <small className="text-muted">
                        Last updated 3 mins ago
                      </small>
                      <br />
                      <br />
                      <br />
                      <hr
                style={{ width: 150, borderTop: "2px solid black", height: 30 }}
              />
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default home;
