import React from "react";
import { NavLink } from "react-router-dom";
const navbar = () => {
  return (
    <div>
      <nav
        className="navbar navbar-expand-lg navbar-light bg-light"
        style={{ padding: 30 }}
      >
        <NavLink
          to="/"
          className="navbar-brand badge badge-primary"
          style={{ fontSize: 50, color: "black" }}
        >
          HK
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent1"
          aria-controls="navbarSupportedContent1"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="container">
          <div className="row ">
            <div className="col-7">
              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                  <li className="nav-item active">
                    <NavLink to="/" className="nav-link">
                      Home <span className="sr-only">(current)</span>
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/contact" className="nav-link">
                      Contact Me
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink to="/about" className="nav-link">
                      About Me
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink to="/resume" className="nav-link">
                      Resume
                    </NavLink>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-4 align-content-lg-end">
              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent1"
              >
                <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
                  <li className="nav-item">
                    <a className="fb-ic">
                      <i className="fa fa-facebook-f fa-lg white-text mr-4">
                        {" "}
                      </i>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="tw-ic">
                      <i className="fa fa-twitter fa-lg white-text mr-4"> </i>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="gplus-ic">
                      <i className="fa fa-google-plus fa-lg white-text mr-4">
                        {" "}
                      </i>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="li-ic">
                      <i className="fa fa-linkedin fa-lg white-text mr-4"> </i>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="ins-ic">
                      <i className="fa fa-instagram fa-lg white-text mr-4"> </i>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="pin-ic">
                      <i className="fa fa-pinterest fa-lg white-text"> </i>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div className="col-1">
              <h3>
                <span
                  className="badge badge-primary align-content-lg-stretch text-right"
                  style={{ color: "black", width: 250 }}
                >
                  PORTFOLIO 
                </span>
              </h3>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default navbar;
